<template>

  <div id="UpdatePass">
    <el-form
        :model="passwordFrom"
        status-icon
        :rules="rules"
        ref="passwordFrom"
        label-width="120px"
    >

      <el-form-item label="" prop="oldPass" label-width="120px">
        <el-input type="password" v-model="passwordFrom.oldPass" @input="validatePassword1" style="width:40%" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="password" label-width="120px">
        <el-input type="password" v-model="passwordFrom.password" @input="validatePassword2" style="width:40%" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="password2" label-width="120px">
        <el-input type="password" v-model="passwordFrom.password2" style="width:40%" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label-width="120px">
        <el-button type="primary" @click="submit"></el-button>
      </el-form-item>
    </el-form>
  </div>

</template>

<script>
import {mapMutations} from 'vuex';

export default {
  name: "ChangePassWord",
  data(){
    return{
      //
      passwordFrom:{
        oldPass:"",
        password: "",
        password2: ""
      },
      //
      rules: {
        oldPass:[
          {
            validator(rule, value, cb) {
              //
              if ( value.length < 6 || value.length > 18) {
                cb(new Error("6~18"))
              }
              //
              if (!/^[\w\[\]\/\\~`|<>,.?;':"{}!@#$%^&*()_+=-]+$/.test(value)) {
                cb(new Error(""))
              }

              cb()
            },
            trigger: "input"
          }
        ],
        password: [
          {
            validator(rule, value, cb) {
              //
              if (value.length < 6 || value.length > 18) {
                cb(new Error("6~18"))
              }
              //
              if (!/^[\w\[\]\/\\~`|<>,.?;':"{}!@#$%^&*()_+=-]+$/.test(value)) {
                cb(new Error(""))
              }

              cb()
            },
            trigger: "input"
          }
        ],
        password2: [
          {
            validator: (rule, value, cb) => {
              if (this.passwordFrom.password !== value) {
                cb(new Error(""))
              }

              if (value.length === 0){
              cb(new Error("") )
            }
              cb()
          },
          }
        ],
      }
    }
  },
  methods:{
    ...mapMutations(['loginOut']),
    validatePassword1(){
        this.$refs.passwordFrom.validateField('oldPass')
    },
    validatePassword2(){
      this.$refs.passwordFrom.validateField('password')
    },
    submit() {
      this.$refs.passwordFrom.validate( async v => {
        if (v) {

          let {data} = await this.$axios({
            method:"POST",
            url:"/personal/password",
            data:{
              OldPassWord:this.passwordFrom.oldPass,
              password:this.passwordFrom.password,
            }
          })

          //
          if (data.code){
            return this.$message.error(data.message)
          }

        //
          this.$message.success("")
          this.loginOut()//vuex
          this.$refs.passwordFrom.resetFields()//
          this.$router.push("/")//


        }else{
          return false
        }
      })
    }
  },
}
</script>

