<template>
  <div class="UpdateName">

    <el-form
        :model="userFrom"
        status-icon
        :rules="rules"
        ref="userFrom"
        label-width="120px"
    >
      <el-form-item label="" label-width="120px" style="width:40%" >
        <span class="code">{{userInfo.username}}</span>
      </el-form-item>
      <el-form-item label="" prop="username" label-width="120px" style="width:40%" >
        <el-input v-model="userFrom.username" placeholder="2~8" ></el-input>
      </el-form-item>
      <el-form-item label-width="120px">
        <el-button type="primary" @click="submit"></el-button>
      </el-form-item>
    </el-form>

  </div>
</template>

<script>
import {mapState,mapMutations} from "vuex"

export default {
  name: "ChangeName",
  data(){
    return{
      userFrom: {
        username:"",
      },
      //
      rules: {
        username: [
          {
            validator: (rule, value, cb) => {

              //
              if (value.length > 8 || value.length === 1) {
                cb(new Error("2~8"))
              }else if (value.length === 0){
                this.$refs['userFrom'].resetFields();
                return;
              }

              //
              if(!/^[\w\u4e00-\u9fa5\u0800-\u4e00\uac00-\ud7ff]+$/.test(value)){
                cb(new Error("   _ //"))
              }

              //
              if (value === this.userInfo.username){
                cb(new Error("..."))
              }

              cb()
            }

          }
        ]
      }
    }
  },

  computed:{
    ...mapState(["userInfo"])
  },

  methods:{
    ...mapMutations(['loginSuccess']),
    submit(){
      this.$refs.userFrom.validate( async v => {
        if (v){
          //
          let {data} = await this.$axios({
            method:"POST",
            url:"/personal/name",
            data:{username:this.userFrom.username}
          })

          //
          if (data.code){
            return this.$message.error(data.message)
          }

        //
          this.$message.success("")
          //vuex
          this.loginSuccess(data.data)
          //
          this.$refs['userFrom'].resetFields();


        }else {
          return false
        }
      })
    },
  },

}
</script>

<style scoped lang="less">

.UpdateName {
  span.code{
    color: #bd4147;
    background-color: rgba(27,31,35,.05);
    font-weight: 700;
    font-size: 20px;
  }
}

</style>