<template>
  <div id="ArticleAdd">
    <el-form
        :model="form"
        status-icon
        ref="ruleForm"
        label-width="120px"
    >
      <el-form-item label="" label-width="120px">
        <el-input
            v-model="form.title"
            placeholder=" md "
            style="width:50%"
        ></el-input>
      </el-form-item>
      <el-form-item label="" label-width="120px">
        <el-input type="textarea" v-model="form.des"  style="width:50%"></el-input>
      </el-form-item>
      <el-form-item label="md" label-width="120px">
        <el-upload
            class="upload-demo"
            drag
            :action="`${baseURL}/adminServer/article/md`"
            :auto-upload="false"
            :on-change="fileChange"
            :on-success="md_upload_success"
            :before-remove="beforeRemove"
            :limit="1"
            ref="mdForm"
            :with-credentials="true"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">.md<em></em></div>
        </el-upload>
      </el-form-item>
      <el-form-item label="" label-width="120px">
        <el-upload
            class="avatar-uploader"
            :action="`${baseURL}/adminServer/article/cover`"
            :show-file-list="false"
            :on-change="coverChange"
            :on-success="cover_upload_success"
            :auto-upload="false"
            ref="imgForm"
            :with-credentials="true"
        >
          <div
              v-if="imageUrl"
              class="avatar"
              :style="{
              backgroundImage: `url(${imageUrl})`
            }"
          >
          </div>
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label-width="100px">
        <el-button type="primary" @click="submitForm"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>

export default {
  name: "AddArticle",
  data(){
    return {
      form: {
        title: "",
        des: "",
        md: "",
        cover: ""
      },
      imageUrl: ""
    }
  },
  methods:{
    //
    submitForm(){
      //md
      this.$refs.mdForm.submit()
    },

    //
    async submit(){
      let {data} = await this.$axios({
        method: "POST",
        url:"/adminServer/article/add",
        data:this.form
      })

      if (data.code) {
        return this.$message.error(data.message)
      }

      //
      await this.$router.push(`/article/${data.data.id}`)
    },

    //md
    fileChange(file,fileList){
        if (file.status !== 'ready')return
      //md
      if (/^(.+)\.md$/.test(file.name)){
        //titlemd
        if (this.form.title) return
        this.form.title = RegExp.$1
      }else {
        //
        this.$message.error(".md")
        //
        fileList.pop()
      }

    },

    //
    beforeRemove(){
      this.form.title = ""
    },

    //md
    md_upload_success(res){

      //
      if (res.code){
        return this.$message.error("")
      }

      //md
      this.form.md = res.url
      //
      if (this.imageUrl){
        //
        this.$refs.imgForm.submit()
      }else{
        //
        this.submit()
      }

    },

    //
    coverChange(file, fileList) {
      if (file.status !== 'ready') return

      const isJPG = /^image\/(jpeg|png)$/.test(file.raw.type);
      const isLt2M = file.raw.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(' JPG/PNG !');
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      if (isJPG && isLt2M) {
        this.imageUrl = URL.createObjectURL(file.raw);
      } else {
        this.imageUrl = ""
        fileList.pop()
      }
    },

    //
    cover_upload_success(res) {
      if (res.code) {
        return this.$message.error("")
      }
      this.form.cover = res.url
      this.submit()
    }

  },
}
</script>

<style scoped lang="less">
  #ArticleAdd{
    /deep/ .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }

    /deep/ .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }

    /deep/ .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
    }

    /deep/ .avatar {
      width: 178px;
      height: 178px;
      display: block;
      background: none center center/cover;
    }
  }
</style>